import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

const routes = [
    {
        path: '/',
        redirect: {name: 'backend'},
    },
    {
        path: '/bigScreen',
        name: 'bigScreen',
        component: () => import('@/page/console'),
        meta: {
            title: '大屏数据',
        },
    },
    {
        path: '/backend',
        name: 'backend',
        component: () => import('@/page/backend'),
        meta: {
            title: '后台管理',
        },
    },
    {
        path: '/userManage',
        name: 'userManage',
        component: () => import('@/page/userManage'),
        meta: {
            title: '用户管理',
        },
    },
    {
        path: '/oneScreen',
        name: 'oneScreen',
        component: () => import('@/layout/oneScreen'),
        meta: {
            title: '某某数据大屏',
        },
        children: [
            {
                path: '/securityMonitor',
                name: 'securityMonitor',
                component: () => import('@/page/securityMonitor'),
                meta: {
                    title: '安全事件监控',
                },
            },
            {
                path: '/attackAnalysis',
                name: 'attackAnalysis',
                component: () => import('@/page/attackAnalysis'),
                meta: {
                    title: '攻击态势分析',
                },
            },
            {
                path: '/appAnalysis',
                name: 'appAnalysis',
                component: () => import('@/page/appAnalysis'),
                meta: {
                    title: '应用安全分析',
                },
            },
            {
                path: '/attackTracking',
                name: 'attackTracking',
                component: () => import('@/page/attackTracking'),
                meta: {
                    title: '攻击来源回溯',
                },
            },
        ],
    },
];

const router = new VueRouter({
    routes, // (缩写) 相当于 routes: routes
});

const setStore = (key, value) => {
    localStorage.setItem(key, value);
};

const getStore = key => localStorage.getItem(key);
const hasStore = () => getStore('token') && getStore('roler') && getStore('uid') && getStore('cookie');

router.beforeEach((to, from, next) => {
    // console.log(to, from, next);
    /**
     * 先检查 drt query > local
     * 存在：
     *      检查 roler
     *          admin 后台管理（放行）
     *          user: 大屏数据
     *
     * 没有：
     * 跳转到 :8080/Dashboard/index.do
     */

    if (to.path === '/backend') {
        if (to.query.d && to.query.r && to.query.t && to.query.c) {
            // console.log('自带权限');
            setStore('token', to.query.t);
            setStore('roler', to.query.r);
            setStore('uid', to.query.d);
            setStore('cookie', to.query.c);
            next();
        } else if (hasStore()) {
            // console.log('本地权限');
            next();
        } else {
            // console.log('没有权限，返回控制中心');
            localStorage.clear();
            window.location.replace(window.MYHOST);
            next(false);
        }
    } else if (hasStore()) {
        // console.log('本地权限');
        next();
    } else {
        // console.log('没有权限，返回控制中心');
        localStorage.clear();
        window.location.replace(window.MYHOST);
        next(false);
    }
});

export default router;
